<template>
    <div id="article">
      <content-header :article="article"></content-header>
      <div v-html="article.body" class="markdown-body" v-marked></div>
      <comment></comment>
    </div>
</template>
<script>
import { getRepoIssue } from '@/api';
import { convertBlogArticle } from '@/utils/helper'
import Comment from '@/components/Comment'
import ContentHeader from '@/components/ContentHeader'

export default {
  components: {
    Comment,
    ContentHeader
  },
  data() {
    return {
      article:{},
      comment:{},
      isProgress: false,
      repoName: 'guanpengchn.github.io'
    }
  },
  created(){
    this.getArticles()
  },
  /* eslint-disable */
  methods: {
    getArticles: function(){
      this.isProgress = true
      getRepoIssue(this.repoName,this.$route.params.id)
        .then(article => {
          this.article = convertBlogArticle(article) 
          this.isProgress = false
        })
    }
  }
};
</script>
<style src="../styles/base.css"></style>
<style src="../styles/atom-one-dark.css"></style>
<style src="../styles/markdown.css"></style>



